.square1 {
    font-family: helvetica, arial, sans-serif;
}

.square1 img {
    display: none;
}

.slides_container {
    position: relative;
    height: 100%;
    overflow: hidden;
}

.image_wrapper,
.wrap_placeholder {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: none;
    background-size: 0;
    background-position: center center;
    background-repeat: no-repeat;
    transition: transform 1s ease-in-out;
}

.image_wrapper.no_transition,
.wrap_placeholder.no_transition {
    transition: none !important;
}

.image_wrapper > a {
    display: block;
    width: 100%;
    height: 100%;
}

.square1_next_image,
.square1_prev_image {
